"use client";

import { useState } from "react";
import { Send, Bot, MessageCircle } from "lucide-react";
import type { SupportedLocale } from "@/data/translations";
import { getTranslations } from "@/data/translations";

type Message = {
  role: "user" | "assistant";
  content: string;
};

export function ChatPanel({ locale }: { locale: SupportedLocale }) {
  const t = getTranslations(locale);
  const [isOpen, setIsOpen] = useState(false);
  const [messages, setMessages] = useState<Message[]>([
    { role: "assistant", content: t.chat.greeting },
  ]);
  const [input, setInput] = useState("");
  const [isSending, setIsSending] = useState(false);

  const handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    if (!input.trim()) return;
    const question = input.trim();
    setMessages((prev) => [...prev, { role: "user", content: question }]);
    setInput("");
    setIsSending(true);

    const reply = await mockAssistantReply(question, locale);
    setMessages((prev) => [...prev, { role: "assistant", content: reply }]);
    setIsSending(false);
  };

  return (
    <div
      className="fixed bottom-6 right-6 z-40 w-full max-w-sm"
      suppressHydrationWarning
    >
      <button
        onClick={() => setIsOpen((prev) => !prev)}
        className="flex w-full items-center justify-between rounded-full bg-gradient-to-r from-blue-500 to-sky-400 px-5 py-3 text-sm font-semibold text-white shadow-xl"
        aria-expanded={isOpen}
        aria-controls="ai-chat-panel"
        suppressHydrationWarning
      >
        <span className="flex items-center gap-2">
          <Bot className="size-4" aria-hidden />
          {isOpen
            ? locale === "es"
              ? "Cerrar asistente"
              : "Hide assistant"
            : t.chat.title}
        </span>
        <MessageCircle className="size-4" aria-hidden />
      </button>

      {isOpen && (
        <div
          id="ai-chat-panel"
          className="mt-4 flex h-[400px] flex-col overflow-hidden rounded-3xl border border-white/70 bg-white/90 shadow-2xl backdrop-blur-xl"
        >
          <div className="flex-1 space-y-3 overflow-y-auto px-5 py-4 text-sm text-slate-700">
            {messages.map((message, index) => (
              <div
                key={`${message.role}-${index}`}
                className={message.role === "user" ? "text-right" : "text-left"}
              >
                <span
                  className={
                    message.role === "user"
                      ? "inline-block rounded-2xl bg-blue-500 px-4 py-2 text-white shadow"
                      : "inline-block rounded-2xl bg-white/80 px-4 py-2 text-slate-700 shadow"
                  }
                >
                  {message.content}
                </span>
              </div>
            ))}
            {isSending && (
              <p className="text-xs text-slate-400">
                {locale === "es" ? "Pensando…" : "Thinking…"}
              </p>
            )}
          </div>
          <form onSubmit={handleSubmit} className="flex items-center gap-2 border-t border-slate-200 bg-white/90 px-4 py-3">
            <label htmlFor="ai-question" className="sr-only">
              {locale === "es" ? "Pregunta" : "Question"}
            </label>
            <input
              id="ai-question"
              value={input}
              onChange={(event) => setInput(event.target.value)}
              placeholder={t.chat.placeholder}
              className="flex-1 rounded-full border border-slate-200 bg-white px-3 py-2 text-sm focus:border-blue-300 focus:outline-none"
            />
            <button
              type="submit"
              disabled={isSending}
              className="inline-flex items-center justify-center rounded-full bg-blue-500 p-2 text-white shadow hover:bg-blue-600 disabled:opacity-50"
              aria-label={locale === "es" ? "Enviar consulta" : "Send question"}
            >
              <Send className="size-4" aria-hidden />
            </button>
          </form>
        </div>
      )}
    </div>
  );
}

async function mockAssistantReply(question: string, locale: SupportedLocale) {
  const lower = question.toLowerCase();
  if (lower.includes("apr")) {
    return locale === "es"
      ? "APR convierte tarifas en un costo anualizado. Compara siempre con el límite de tu estado en la página de regulaciones."
      : "APR converts flat fees into an annualized cost. Always compare against your state cap inside the regulations hub.";
  }
  if (lower.includes("same-day") || lower.includes("mismo dia")) {
    return locale === "es"
      ? "Los depósitos el mismo día dependen del horario del prestamista y de tu banco; envía documentos temprano para aumentar las probabilidades."
      : "Same-day deposits depend on lender cut-off times and your bank; submit docs early to improve the odds.";
  }
  return locale === "es"
    ? "Estoy recopilando datos. Visita el hub de tu estado para reglas detalladas o pregunta por montos específicos."
    : "I'm gathering context. Visit your state hub for deeper rules or ask about specific loan amounts.";
}
