"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";
import { useMemo } from "react";
import { getNavigation } from "@/data/navigation";
import { getTranslations, type SupportedLocale } from "@/data/translations";
import { cn } from "@/lib/utils";

export function SiteHeader({ locale }: { locale: SupportedLocale }) {
  const pathname = usePathname();
  const navigation = useMemo(() => getNavigation(locale), [locale]);
  const translations = useMemo(() => getTranslations(locale), [locale]);

  return (
    <header className="sticky top-0 z-30 backdrop-blur-xl bg-white/70 border-b border-white/50">
      <div className="mx-auto flex max-w-6xl items-center justify-between gap-6 px-4 py-4 lg:px-8">
        <Link href={locale === "es" ? "/es" : "/"} className="flex items-center gap-3">
          <div className="rounded-2xl bg-gradient-to-br from-blue-500 to-sky-400 p-2 text-white shadow-lg">
            <span className="text-lg font-bold">AI</span>
          </div>
          <div className="flex flex-col">
            <span className="text-sm font-medium text-slate-500 uppercase tracking-[0.25em]">
              Loan Portal
            </span>
            <span className="-mt-1 text-xl font-semibold text-slate-900">
              {translations.header.tagline}
            </span>
          </div>
        </Link>

        <nav className="hidden items-center gap-5 lg:flex">
          {navigation.primary.map((item) => (
            <Link
              key={item.href}
              href={item.href}
              className={cn(
                "text-sm font-medium text-slate-600 transition-colors hover:text-slate-900",
                pathname.startsWith(item.href) && "text-slate-900",
              )}
            >
              {item.label}
            </Link>
          ))}
        </nav>

        <div className="flex items-center gap-3">
          <LocaleSwitcher locale={locale} pathname={pathname} />
          <Link
            href={locale === "es" ? "/es/compare" : "/compare"}
            className="hidden sm:inline-flex btn-primary px-5 py-3 rounded-xl text-sm tracking-tight shadow-lg shadow-blue-500/10"
          >
            {locale === "es" ? "Pre-calificar" : "Pre-qualify"}
          </Link>
        </div>
      </div>
    </header>
  );
}

function LocaleSwitcher({
  locale,
  pathname,
}: {
  locale: SupportedLocale;
  pathname: string;
}) {
  const target = locale === "es" ? pathname.replace(/^\/es/, "") || "/" : `/es${pathname === "/" ? "" : pathname}`;

  return (
    <Link
      href={target}
      prefetch={false}
      className="text-sm font-semibold text-slate-500 hover:text-slate-900"
    >
      {locale === "es" ? "EN" : "ES"}
    </Link>
  );
}
